import PropTypes from "prop-types";
import { useState, useEffect } from "react";
import {
  Container,
  Button,
  Tweet,
  Followers,
  ButtonFollowing,
  Text,
  Logo,
  ImageContainer,
} from "./Card.styled";
import Image from "../image/Image";
import goit_logo from "../../images/goit_logo.png";
import picture from "../../images/picture.png";

const Card = (users) => {
  const { id, tweets, followers, avatar, user } = users;

  const [isFollowing, setIsFollowing] = useState(
    JSON.parse(localStorage.getItem(`${id}-isFollowing`)) ?? false
  );
  const [numberFollowers, setNumberFollowers] = useState(
    JSON.parse(localStorage.getItem(`${id}-followers`)) ?? followers
  );

  useEffect(() => {
    localStorage.setItem(`${id}-isFollowing`, JSON.stringify(isFollowing));
    localStorage.setItem(`${id}-followers`, JSON.stringify(numberFollowers));
  }, [isFollowing, numberFollowers, id]);

  const onFollowClick = () => {
    setIsFollowing(true);
    setNumberFollowers(numberFollowers + 1);
  };

  const onFollowingClick = () => {
    setIsFollowing(false);
    setNumberFollowers(numberFollowers - 1);
  };

  return (
    <Container>
      <Logo src={goit_logo} alt="logo" width="76" />
      <ImageContainer src={picture} alt="picture" />
      <Image image={avatar} alt={user} />

      <Text>
        <Tweet>{tweets} tweets</Tweet>
        <Followers>
          {numberFollowers.toLocaleString("en-US")} followers
        </Followers>

        {isFollowing ? (
          <ButtonFollowing type="button" onClick={onFollowingClick}>
            Following
          </ButtonFollowing>
        ) : (
          <Button type="button" onClick={onFollowClick}>
            Follow
          </Button>
        )}
      </Text>
    </Container>
  );
};

Card.propTypes = {
  user: PropTypes.string.isRequired,
  avatar: PropTypes.string.isRequired,
  tweets: PropTypes.number.isRequired,
  followers: PropTypes.number.isRequired,
};

export default Card;
