import styled from "styled-components";

export const Container = styled.div`
  background-image: url("../../img/picture.png");
  background-repeat: no-repeat;
  background-position: center;
  height: 168px;
  width: 308px;
`;
export const ContainerFoto = styled.div`
  background-color: #5736a3;
  width: 80px;
  height: 80px;
  border: 8px solid #ebd8ff;
  box-shadow: 0px 4.391631126403809px 3.2937235832214355px 0px #fbf8ff,
    inset 0px 4.391631126403809px 4.391631126403809px 0px #0000000f,
    insert 0px -2.1958155632019043px 4.391631126403809px 0px #ae7be3;

  border-radius: 50%;
  position: absolute;
  top: 178px;
  left: 150px;
`;

export const Img = styled.img`
  border-color: green;
  border-radius: 50%;
`;
