import PropTypes from "prop-types";
import { Button, Wrapper } from "./GoBack.styled";


const GoBack = ({ previousLocation: { current } }) => {
  return (
    <Button to={current}>
      <Wrapper>
        GO BACK
      </Wrapper>
    </Button>
  );
};

GoBack.propTypes = {
  previousLocation: PropTypes.object.isRequired,
};

export default GoBack;
